.LogoType {
    background-image: url(../img/logotype.png);
    width: 100%;
    height: 100%;
    background-size: contain;
    opacity: 1;
    background-repeat: no-repeat;
    background-position: center;
}
.CoverController {
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    height: 23vh;
    bottom: 10vmin;
    margin-top: 60vmin;
    margin-bottom: 10vmin;
    padding-left: 10vmin;
    padding-right: 10vmin;
}
.copyrightText {
    color: rgb(255, 255, 255);
    width: 100%;
    height: 10em;
    position: absolute;
    left: 0;
    letter-spacing: 0.3em;
    font-size: 0.3em;
    line-height: 4em;
    opacity: 1;
}
.decoration-front {
    width: 270vw;
    height: 20vh;
    background: linear-gradient(to right, rgb(255 255 255 / 100%), rgb(255 255 255 / 0%));
    position: absolute;
    transform: rotate(138deg) scale(0, 0);
}
.decoration-front-slim {
    width: 270vw;
    height: 5vh;
    left: 45vh;
    background: linear-gradient(to right, rgb(255 255 255 / 100%), rgb(255 255 255 / 0%));
    position: absolute;
    transform: rotate(138deg) scale(0, 0);
}
.decoration-back {
    position: absolute;
    width: 300vw;
    height: 20vh;
    background: linear-gradient(to right, rgb(255 255 255 / 100%), rgb(255 255 255 / 0%));
    left: -55vw;
    top: -84vh;
    transform: rotate(138deg) scale(0, 0);
}

/*決定排序的整體定位*/
.character-wrap {
    position: absolute;
    box-sizing: border-box;
    width: 100vw;
    height: 100vh;
}

/*各別角色的left.top.scale如何作用在角色身上*/
/*x座標要根據畫面寬度(vw)做調整，寬度越小，這個值要越小*/
.character-sort {
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    background-size: auto calc(100% * var(--offset-scale));
    background-position-x: calc(50% + var(--offset-left) * var(--offset-shrink));
    background-position-y: calc(var(--offset-top) + var(--offset-top-rwd));
    background-repeat: no-repeat;
    opacity: 0;
}

.character-sort::after {
    content: '';
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    background-size: auto calc(100% * var(--offset-scale));
    background-position-x: calc(50% + var(--offset-left) * var(--offset-shrink));
    background-position-y: calc(var(--offset-top) + var(--offset-top-rwd));
    background-repeat: no-repeat;
}

.character-shrink {
    --offset-shrink: 1;
    --offset-top-rwd: 0;
}

/*置中*/
@media (max-width: 767.99px) {
    .copyrightText {
        text-align: center;
    }
    .character-shrink {
        --offset-shrink: 0.7;
        --offset-top-rwd: 5vh;
    }
    .CoverController {
        bottom: 10vmin;
    }
}
@media (min-width: 768px) and (max-width: 979px) {
    .copyrightText {
        text-align: center;
    }
    .character-shrink {
        --offset-shrink: 0.7;
        --offset-top-rwd: 5vh;
    }
}
@media (min-width: 980px) {
    .copyrightText {
        text-align: center;
    }
    .decoration-front {
        left: 14vh;
    }
    .decoration-front-slim {
        left: 30vh;
        top: 15vh;
    }
    .character-shrink {
        --offset-shrink: 0.8;
    }
    .character-shrink {
        --offset-shrink: 1;
        --offset-top-rwd: 0px;
    }
}

/*置右*/
@media (min-width: 1200px) {
    .CoverController {
        padding-left: 60vw;
    }
    .copyrightText {
        line-height: 3em;
        text-align: left;
        bottom: 10vmin;
        left: 10vmin;
    }
    .decoration-front {
        left: 38vh;
        top: -70vh;
    }
    .decoration-front-slim {
        left: 51vh;
        top: -50vh;
    }
    .character-shrink {
        --offset-shrink: 1;
        --offset-top-rwd: 0px;
    }
}
@media (min-width: 1600px) {
    /* .CoverController {
        padding-left: 70vmin;
        margin-top: 52vmin;
    } */
    .character-shrink {
        --offset-shrink: 1;
        --offset-top-rwd: 0px;
    }
}
