.mabinButton {
    color: #ffffff;
    margin-top: 10px;
    background-color: transparent;
    border-image: url(../img/gui/uiButton_idle.png);
    border-image-slice: 2 fill;
    border-image-width: 2px;
}

.mabinButton:hover {
    border-image: url(../img/gui/uiButton_hover.png);
    border-image-slice: 2 fill;
    border-image-width: 2px;
}

.mabinButton:active {
    border-image: url(../img/gui/uiButton_down.png);
    border-image-slice: 2 fill;
    border-image-width: 2px;
}

.mabinButton {
    justify-self: flex-end;
    margin-right: 1em;
}
.mabinButton:hover {
    cursor: url(../img/gui/cursors/select.png), auto;
}

.mabinContent {
    flex: 1;
}

#gameContainer {
    cursor: url(../img/gui/cursors/normal.png), auto;
}

#mabinButtonArea {
    display: flex;
    justify-self: flex-end;
    flex-direction: row;
}

#frameContainer {
    -webkit-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    user-select: none;
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-content: center;
    align-items: flex-start;
    position: absolute;
    width: 100%;
    height: 100%;
}
#mabinCharacter {
    position: absolute;
    height: 100%;
}
#mabinBackground {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgb(64, 177, 230);
}
#mabinFrame {
    display: flex;
    flex-direction: column;
    align-content: flex-start;
    justify-content: flex-start;
    align-items: flex-start;
    flex: 0.55;
    align-self: flex-end;
    border-image: url(../img/gui/uiFrame.png);
    border-image-slice: 10 fill;
    border-image-width: 10px;
    width: 50rem;
    height: 10rem;
    opacity: 0.9;
    padding: 20px;
    padding-left: 50px;
    padding-right: 50px;
    margin-bottom: 2rem;
    image-rendering: pixelated; /* Chrome */
    image-rendering: optimizeSpeed; /* STOP SMOOTHING, GIVE ME SPEED  */
    image-rendering: -moz-crisp-edges; /* Firefox                        */
    image-rendering: -o-crisp-edges; /* Opera                          */
    -ms-interpolation-mode: nearest-neighbor; /* IE8+                           */
}

@keyframes chatBubbleEnter {
    0% {
        transform: scale(0, 0);
    }
    20% {
        transform: scale(0.2, 1);
    }
    100% {
        transform: scale(1, 1);
    }
}
@keyframes chatBubbleOut {
    0% {
        transform: scale(1, 1);
    }
    20% {
        transform: scale(0.8, 1);
    }
    100% {
        transform: scale(0, 0);
    }
}
@keyframes chatBubbleStatic {
    0% {
        transform: scale(0, 0);
    }
}

div.chatBubble{
    opacity: 0.9;
    padding: 20px;
    padding-left: 20px;
    padding-right: 20px;
    border-image: url(../img/gui/chatBubble.png);
    border-image-slice: 20 fill;
    border-image-width: 10px;
    transform: scale(0, 0);
    color: #5a0f0f;
    transform-origin: bottom right;
}

.cursorStop {
    cursor: url(../img/gui/cursors/stop.png), auto;
}
.cursorSkip {
    cursor: url(../img/gui/cursors/skip.png), auto;
}
