@keyframes NextButton {
    0% {
        transform: translate(0, 10px);
    }
    100% {
        transform: translate(0, 0);
    }
}

@keyframes ScaleIn-text {
    0% {
        transform: scale(0, 0);
    }
    100% {
        transform: scale(1, 1);
    }
}

@keyframes FadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes FadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes WaveIn {
    0% {
        opacity: 0;
        transform: translateY(10px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes WaveOut {
    0% {
        opacity: 1;
        transform: translateY(0px);
    }
    100% {
        opacity: 0;
        transform: translateY(-10px);
    }
}

div.OnePage {
    box-sizing: border-box;
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}
div.UponPage{
    position: "absolute";
    width: 100%;
    height: 100%;
}

div.HoverToScale {
    transform: scale(1, 1);
    transform-origin: 70em 23em;
    transition: transform 0.1s;
}
div.HoverToScale:hover {
    transform: scale(1.15, 1.15);
}
div.NextButton {
    position: absolute;
    width: 13em;
    left: calc(100% - 26em);
    top: calc(100% + 25em);
    height: 5em;
    background-image: url(../img/gui/arrow.svg);
    background-repeat: no-repeat;
    background-size: contain;
    filter: drop-shadow(0 0 6px yellow);
    opacity: 0;
    animation: NextButton 0.5s cubic-bezier(0, 0, 0.1, 1.01) infinite alternate, FadeIn 1s ease 4s forwards;
}
div.NextButton:hover {
    animation-play-state: paused;
}

div.FlexHoverToScale {
    margin-top: 2vh;
    transform: scale(1, 1);
    transition: transform 0.1s;
}
div.FlexHoverToScale:hover {
    transform: scale(1.15, 1.15);
}

div.exhibitiButton-wrap{
    margin-top: 5vmin;
    width: 6vmin;
    height: 5vmin;
    transform: scale(1, 1);
    transition: transform 0.1s;
}
div.exhibitiButton-wrap:hover {
    transform: scale(1.15, 1.15);
}
div.exhibitiButton {
    width: 100%;
    height: 100%;
    left: calc( 50% - 6vmin );
    background-image: url(../img/gui/arrow.svg);
    background-repeat: no-repeat;
    background-size: contain;
    filter: drop-shadow(0 0 6px yellow);
    transform-origin: center;
    opacity: 0;
    animation: NextButton 0.5s cubic-bezier(0, 0, 0.1, 1.01) infinite alternate, FadeIn 1s ease 0s forwards;
}
div.exhibitiButton:hover {
    animation-play-state: paused;
}

div.noAnim {
    animation: FadeOut 0.2s ease-out forwards;
}