@keyframes LogoEnter {
    0% {
        transform: translate(0px, 5em);
        opacity: 0;
    }
    100% {
        transform: translate(0px, 0px);
        opacity: 1;
    }
}

@keyframes LogoIdle {
    0% {
        filter: brightness(1) drop-shadow(0 0 2rem white);
    }
    100% {
        filter: brightness(1.5) drop-shadow(0 0 2rem white);
    }
}

@keyframes FadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes FadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes ScaleIn {
    0% {
        transform: rotate(138deg) scale(1, 0);
        filter: blur(10px);
    }
    100% {
        transform: rotate(138deg) scale(1, 1);
        filter: blur(0px);
    }
}