div.exhibitcontent {
    position: relative;
    height: 100vh;
    top: 0;
    width: 50vw;
    float: left;
    box-sizing: border-box;
    padding: 10vmin 10vmin 20vmin 10vmin;
}
div.exhibitcontent-wrap {
    position: absolute;
    bottom: 10vmin;
}
div.exhibitcontent_asLeft {
    padding-left: 20vmin;
    padding-right: 5vmin;
    padding-bottom: 10vmin;
    padding-top: 30vmin;
}
div.exhibitcontent_asRight {
    padding-left: 5vmin;
    padding-right: 20vmin;
    padding-bottom: 10vmin;
    padding-top: 30vmin;
}
div.infoTitle {
    color: white;
    font-size: 3em;
    letter-spacing: 0.1em;
    text-shadow: 0 0 13px #ff6f6f;
    margin-bottom: 1em;
    width: 100%;
}
div.chat-wrap {
    position: relative;
    height: 50%;
    width: 120%;
    top: 50%;
}
.infocontentIn {
    animation: WaveIn 0.5s ease-in 1s forwards;
}
.infocontentOut {
    animation: WaveOut 1s ease-in 2s forwards;
}
div.infocontent {
    color: white;
    font-size: 1em;
    letter-spacing: 0.1em;
    line-height: 1.5em;
    opacity: 0;
    text-shadow: -1.5px 1px 2px #ce8a8a;
    width: 100%;
}
div.infoPictureArea {
    width: 100%;
    height: 100%;
    position: absolute;
    background-size: contain;
    animation: WaveIn 1s cubic-bezier(0.04, 0.83, 0.35, 1.58) 0s forwards;
}

/*置中*/
@media (max-width: 767.99px) {
    div.infoPictureArea {
        width: 100%;
        height: 100%;
        position: absolute;
        background-size: cover;
        animation: WaveIn 1s cubic-bezier(0.04, 0.83, 0.35, 1.58) 0s forwards;
        filter: blur(2px);
        background-position: center;
    }
    div.infoTitle {
        margin-top: 100%;
    }
    div.chat-wrap {
        width: 100%;
    }
    div.exhibitcontent {
        position: relative;
        height: 50vh;
        top: 0;
        width: 100vw;
        box-sizing: border-box;
    }
    div.exhibitcontent_asLeft {
        padding-left: 20vmin;
        padding-right: 20vmin;
        padding-bottom: 10vmin;
        padding-top: 50vmin;
    }
    div.exhibitcontent_asRight {
        padding-left: 20vmin;
        padding-right: 40vmin;
        padding-bottom: 30vmin;
        padding-top: 0vmin;
    }
    div.exhibitcontent-wrap {
        position: absolute;
        bottom: 0vmin;
        width: 60%;
    }
}
@media (min-width: 768px) and (max-width: 979px) {
    div.infoTitle {
        margin-top: 50%;
    }
    div.chat-wrap {
        width: 80%;
    }
    div.exhibitcontent_asLeft {
        padding-left: 20vmin;
        padding-right: 5vmin;
        padding-bottom: 10vmin;
        padding-top: 30vmin;
    }
    div.exhibitcontent_asRight {
        padding-left: 5vmin;
        padding-right: 20vmin;
        padding-bottom: 10vmin;
        padding-top: 30vmin;
    }
    div.exhibitcontent-wrap {
        position: absolute;
        bottom: 10vmin;
    }
}
@media (min-width: 980px) {
    div.infoTitle {
        margin-top: 20%;
    }
    div.chat-wrap {
        width: 100%;
    }
}
/*置右*/
@media (min-width: 1200px) {
    div.infoTitle {
        margin-top: 30%;
    }
    div.chat-wrap {
        width: 100%;
    }
}
@media (min-width: 1600px) {
}
